import { FC } from "react";
import classNames from "classnames/bind";

import { Link, LinkType } from "common/components/Link";
import { Button, ButtonSize } from "common/components/new/Button";
import {
    getAnalyticsId,
    GetAnalyticsIdElement,
} from "common/utils/AnalyticsId";
import { CountryVersion, LogoTheme, Theme } from "common/constants/enum";
import { Icon } from "common/components/Icons";
import LogoTeensIcon from "common/components/Icons/icons/logo-teens.svg";
import LogoIcon from "common/components/Icons/icons/logo.svg";
import SmilikeLogo from "common/components/Icons/icons/smilike-logo.svg";
import OrthosLogo from "common/components/Icons/icons/orthos-logo.svg";
import LogoMonoIcon from "common/components/Icons/icons/logo-mono.svg";

import { ContactsList } from "./ContactsList";
import { HeaderShortProps } from "./HeaderShort.types";
import styles from "./HeaderShort.module.scss";

const cx = classNames.bind(styles);

export const HeaderShort: FC<HeaderShortProps> = ({
    version,
    wa,
    site,
    email,
    phone,
    theme = Theme.DARK,
    logoTheme = LogoTheme.BICOLOR,
    button,
    className,
    isMailPage = false,
    isAbsolute,
    isFixedButtonActive = true,
    isTeens = false,
}) => {
    const contacts = wa || email || phone ? { wa, email, phone } : null;
    let Logo = logoTheme === LogoTheme.BICOLOR ? LogoIcon : LogoMonoIcon;

    if (version === CountryVersion.UKRAINE) {
        Logo = OrthosLogo;
    }

    if (version === CountryVersion.GERMANY) {
        Logo = SmilikeLogo;
    }

    const logoContent = (
        <Icon className={cx("logo-icons")}>
            <Logo className={cx("logo-icon")} />
            {isTeens && <LogoTeensIcon className={cx("logo-teens-icon")} />}
        </Icon>
    );

    return (
        <>
            <header
                className={cx("root", className, {
                    root_absolute: isAbsolute,
                    "root_type-germany": version === CountryVersion.GERMANY,
                })}
            >
                {site ? (
                    <Link
                        className={cx("logo", {
                            logo_light: theme === Theme.LIGHT,
                            logo_brand: theme === Theme.BRAND,
                        })}
                        {...site}
                        type={LinkType.withoutName}
                        id={getAnalyticsId({
                            zone: "header",
                            element: GetAnalyticsIdElement.LINK,
                            label: "logo",
                        })}
                    >
                        {logoContent}
                    </Link>
                ) : (
                    <div
                        className={cx("logo", {
                            logo_light: theme === Theme.LIGHT,
                            logo_brand: theme === Theme.BRAND,
                        })}
                    >
                        {logoContent}
                    </div>
                )}
                {contacts && !isMailPage && (
                    <div className={cx("desktop-menu")}>
                        <ContactsList
                            {...contacts}
                            theme={theme}
                            version={version}
                        />
                    </div>
                )}
            </header>

            {isFixedButtonActive && button && (
                <div
                    className={cx("button-fixed-wrapper", {
                        "button-fixed-wrapper_version-germany":
                            version === CountryVersion.GERMANY,
                    })}
                >
                    <Button
                        onClick={button.onClick}
                        className={cx(button?.className, "button-fixed")}
                        link={button?.link}
                        theme={button?.theme}
                        size={ButtonSize.M}
                        isMaxWidth={true}
                        version={version}
                    >
                        {button.name}
                    </Button>
                </div>
            )}
        </>
    );
};
