import { FC } from "react";
import classNames from "classnames/bind";

import {
    getAnalyticsId,
    GetAnalyticsIdElement,
} from "common/utils/AnalyticsId";
import { Link } from "common/components/Link";
import { Text, TextTag } from "common/components/Text";
import { Icon } from "common/components/Icons";
import PhoneIcon from "common/components/Icons/icons/24/phone.svg";
import WhatsappIcon from "common/components/Icons/icons/24/whatsapp.svg";
import MailIcon from "common/components/Icons/icons/24/mail.svg";
import { CountryVersion, Theme } from "common/constants/enum";

import { ContactsListProps } from "./ContactsList.types";
import styles from "./ContactsList.module.scss";

const cx = classNames.bind(styles);

export const ContactsList: FC<ContactsListProps> = ({
    phone,
    email,
    wa,
    theme,
    version,
}) => (
    <nav
        className={cx("menu", {
            "menu_type-germany": version === CountryVersion.GERMANY,
        })}
    >
        {phone && (
            <Link
                className={cx("link", {
                    link_light: theme === Theme.LIGHT,
                })}
                link={phone.link}
                id={getAnalyticsId({
                    zone: "menu",
                    element: GetAnalyticsIdElement.LINK,
                    label: "tel",
                })}
            >
                <Icon>
                    <PhoneIcon />
                </Icon>
                <Text className={cx("link-text")} tag={TextTag.P}>
                    {phone.name}
                </Text>
            </Link>
        )}
        {wa && (
            <Link
                id={getAnalyticsId({
                    zone: "menu",
                    element: GetAnalyticsIdElement.LINK,
                    label: "wa",
                })}
                className={cx("link", {
                    link_light: theme === Theme.LIGHT,
                })}
                link={wa.link}
            >
                <Icon>
                    <WhatsappIcon />
                </Icon>
                <Text className={cx("link-text")} tag={TextTag.P}>
                    {wa.name}
                </Text>
            </Link>
        )}
        {email && (
            <Link
                id={getAnalyticsId({
                    zone: "menu",
                    element: GetAnalyticsIdElement.LINK,
                    label: "email",
                })}
                className={cx("link", {
                    link_light: theme === Theme.LIGHT,
                })}
                link={email.link}
            >
                <Icon>
                    <MailIcon />
                </Icon>
                <Text className={cx("link-text")} tag={TextTag.P}>
                    {email.name}
                </Text>
            </Link>
        )}
    </nav>
);
