import { FC } from "react";
import classNames from "classnames/bind";

import { getAnalyticsId } from "common/utils/AnalyticsId";
import { CountryVersion, Theme } from "common/constants/enum";
import { BookAppointmentFullForm } from "common/components/BookAppointmentFullForm";

import { NewAppointmentProps } from "./NewAppointment.types";
import styles from "./NewAppointment.module.scss";

const cx = classNames.bind(styles);

export const NewAppointment: FC<NewAppointmentProps> = ({
    className,
    version,
    language,
    defaultCity,
    theme = Theme.DARK,
}) => {
    const isGermany = version === CountryVersion.GERMANY;

    return (
        <div className={cx(className, "root")}>
            <BookAppointmentFullForm
                version={version}
                language={language}
                defaultCity={defaultCity}
                analyticId={getAnalyticsId({
                    zone: "main-page",
                })}
                className={cx("form", {
                    form_inter: isGermany,
                })}
                theme={theme}
            />
        </div>
    );
};
