import { FC, useEffect, useRef } from "react";
import classNames from "classnames/bind";

import { Container } from "common/components/Container";
import { MaxWidthContainer } from "common/components/new/MaxWidthContainer";
import { NewAppointment } from "common/components/NewAppointment";
import { AdvantagesGrid } from "common/components/AdvantagesGrid";
import { AdvantagesBlock } from "common/components/AdvantagesBlock";
import { useComputedFixedButtonOffset } from "common/hooks/useComputedFixedButtonOffset";
import { useFixedButtonContext } from "common/context/fixedButtonContext";
import { Heading, HeadingType } from "common/components/new/Heading";
import { CountryVersion } from "common/constants/enum";

import { Footer } from "components/Footer";

import { Head, HeadProps } from "./components/Head";
import { RemarketingLayoutProps } from "./RemarketingLayout.types";
import styles from "./RemarketingLayout.module.scss";

const cx = classNames.bind(styles);

export const RemarketingLayout: FC<RemarketingLayoutProps> = ({
    content: {
        head,
        advantagesGrid,
        advantagesBlock,
        contacts,
        bookingFormBlock,
    },
    version,
    language,
}) => {
    const appointmentRef = useRef<HTMLDivElement>(null);

    const {
        isFixedButtonActive,
        scrollToAppointment,
    } = useComputedFixedButtonOffset(appointmentRef);

    const { setFixedButton } = useFixedButtonContext();

    useEffect(() => {
        setFixedButton(isFixedButtonActive);
    }, [isFixedButtonActive]);

    return (
        <MaxWidthContainer>
            <main className={cx("root")}>
                <Head
                    {...(head as HeadProps)}
                    contacts={contacts}
                    onButtonClick={scrollToAppointment}
                    isFixedButtonActive={isFixedButtonActive}
                    className={cx("head")}
                />

                <div ref={appointmentRef}>
                    <Heading
                        className={cx("appointment-title", {
                            "appointment-title_ss-pro":
                                version === CountryVersion.GERMANY,
                            "appointment-title_germany":
                                version === CountryVersion.GERMANY,
                        })}
                        type={HeadingType.M}
                    >
                        {bookingFormBlock.title}
                    </Heading>
                    <Container>
                        <NewAppointment
                            version={version}
                            language={language}
                            className={cx("appointment")}
                        />
                    </Container>
                </div>
                <Container>
                    <AdvantagesGrid
                        {...advantagesGrid}
                        version={version}
                        language={language}
                        onButtonClick={scrollToAppointment}
                    />
                </Container>
                <div className={cx("advantages-block")}>
                    <AdvantagesBlock {...advantagesBlock} />
                </div>
                <Container>
                    <Footer {...contacts} />
                </Container>
            </main>
        </MaxWidthContainer>
    );
};
