import React, { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { HeaderShort } from "common/components/new/HeaderShort";
import { Text, TextTag } from "common/components/Text";
import { Heading, HeadingTag } from "common/components/new/Heading";
import { Video } from "common/components/Video";
import { Picture } from "common/components/Picture";
import { Container } from "common/components/Container";
import { WINDOW_SIZE } from "common/constants/enum";

import { HeadProps } from "./Head.types";
import styles from "./Head.module.scss";

const cx = classNames.bind(styles);

export const Head: FunctionComponent<HeadProps> = ({
    title,
    button,
    isFixedButtonActive,
    contacts,
    subtitle,
    onButtonClick = () => {},
    priceLabel,
    linkVideo,
    linkGif,
    className,
}) => {
    return (
        <section className={cx("root", className)}>
            <Container className={cx("container")}>
                <HeaderShort
                    className={cx("header")}
                    button={{
                        onClick: onButtonClick,
                        name: button.name,
                    }}
                    isFixedButtonActive={isFixedButtonActive}
                    {...contacts}
                    isAbsolute
                />

                <div className={cx("row")}>
                    <div className={cx("column", "column-content")}>
                        <Picture
                            desktop={{
                                png: priceLabel?.desktop.png,
                            }}
                            mobile={{
                                png: priceLabel?.mobile.png,
                            }}
                            classNameImg={cx("price-label")}
                            alt="price-label"
                        />

                        <Heading tag={HeadingTag.H1} className={cx("title")}>
                            {title}
                        </Heading>

                        {subtitle && (
                            <Text tag={TextTag.H2} className={cx("subtitle")}>
                                {subtitle}
                            </Text>
                        )}
                    </div>

                    <div className={cx("column", "column-video")}>
                        {linkVideo && (
                            <Video
                                className={cx("video")}
                                autoPlay={true}
                                loop={true}
                                muted={true}
                                src={linkVideo}
                            />
                        )}

                        <Picture
                            mobile={{
                                gif: linkGif,
                                media: `(max-width: ${WINDOW_SIZE.TABLET}px)`,
                            }}
                            classNameImg={cx("image")}
                            alt="promo"
                        />
                    </div>
                </div>
            </Container>
        </section>
    );
};
